.carousel {
  position: relative;
  max-width: 100%;
  overflow: hidden;
}

.carousel__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 1.5rem;
  border: none;
  padding: 1rem;
  cursor: pointer;
  z-index: 1;
  transition: background-color 0.3s;
}

.carousel__btn:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.carousel__btn--prev {
  left: 0;
}

.carousel__btn--next {
  right: 0;
}

.carousel__img {
  width: 60%;
  height: 500px; 
  display: block;
  transition: transform 0.3s ease-in-out;
}

.carousel__img:hover {
  transform: scale(1.05);
} 
